<template>
  <div class="MapBox">
    <div id="mapLocation"></div>
  </div>
</template>
<script>
// import * as L from "leaflet";
export default {
  name: "ddMap",
  data() {
    return {
      msg: ""
    };
  },
  created() {},
  mounted() {
    this.getMe();
  },
  methods: {
    getMe() {
      if (navigator.geolocation) {
        console.log("/*  */");
        navigator.geolocation.getCurrentPosition(this.h5ApiSuccess, this.h5ApiError);
      } else {
        console.log("/*  */");
        
      }
    },
    h5ApiSuccess:(position) => {
        let latitude = position.coords.latitude; //
        let longitude = position.coords.longitude; //
        console.log('' + longitude + ',' + latitude + '')
    },
    h5ApiError:() => {
        console.log('/*  */')    
    }
  }
};
</script>
<style scoped lang="scss">
.MapBox {
  width: 100%;
  height: 100%;
  #mapLocation {
    width: 100%;
    height: 100%;
  }
}
</style>


